* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}

.header {
  background-color: #1e1e1ffc;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 50px;
  padding: 17px;
  display: flex;
  position: fixed;
}

.header__logo-home {
  text-decoration: none;
}

.header-name {
  color: #f5f5f5;
  text-shadow: -3px 3px 3px #000;
  font-family: Orbitron, sans-serif;
  font-size: 30px;
}

.header__container-link {
  display: none;
}

@media (min-width: 500px) {
  .header__container-link {
    align-items: center;
    display: flex;
  }
}

.header__links {
  color: #fff;
  margin-right: 29px;
  padding: 6px;
  font-family: Poppins, sans-serif;
  font-weight: 400;
  text-decoration: none;
}

.header__container-link a:hover {
  background-color: teal;
  border-radius: 5px;
  transition: all .55s;
}

#navMenu > span {
  background-color: #fbfbfb;
  border-radius: 9999px;
  width: 28px;
  height: 2px;
  display: block;
}

#navMenu > span:not(:last-child) {
  filter: drop-shadow(2px 0 1px #000);
  margin-bottom: 7px;
}

#navMenu, #navMenu > span {
  filter: drop-shadow(1px 1px 3px #000);
  width: 28px;
  transition: all .4s ease-in-out;
}

#navMenu.active {
  transition-delay: .8s;
  transform: rotate(45deg);
}

#navMenu.active > span:nth-child(2) {
  width: 0;
}

#navMenu.active > span:first-child, #navMenu.active > span:nth-child(3) {
  transition-delay: .4s;
}

#navMenu.active > span:first-child {
  transform: translateY(9px);
}

#navMenu.active > span:nth-child(3) {
  transform: translateY(-9px)rotate(90deg);
}

@media (min-width: 500px) {
  #navMenu {
    display: none;
  }
}

.button-open-img {
  width: 30px;
  height: 35px;
}

#header__ventana-burger {
  text-align: center;
  z-index: 9;
  background: #8af1ff;
  border-radius: 22px;
  flex-direction: column;
  justify-content: space-evenly;
  width: 100%;
  transition: all .5s;
  display: flex;
  position: fixed;
  top: 52px;
  right: -100%;
  box-shadow: 1px 3px 3px 3px #000;
}

#header__ventana-burger.active {
  transition-delay: .1s;
  right: 0;
}

.header__ventana-burger-content {
  background-color: #417670;
  border-radius: 9px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 548px;
  padding: 5px 21px;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 8px;
}

@media (min-width: 500px) {
  .header__ventana-burger-content {
    display: none;
  }
}

.ventana__close {
  background-color: #0000;
  border: none;
  align-self: flex-end;
